___CSS_LOADER_EXPORT___.push([module.id, `li {
  margin-left: 2ch;
}

a {
  color: inherit;
  &:hover {
    text-decoration: none;
  }
}

h3 {
  font-weight: normal;
  font-size: 24px;
  line-height: 27px;
}

p {
  font-size: 16px;
  line-height: 27px;
}

.picture-caption {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1em;
  padding: 1em;
}

.more-work {
  display: flex;
  justify-content: center;
  padding: 5% 10%;
  gap: 2em;
}

.header-intro {
  display: flex;
  gap: 5px;
  align-items: center;
  width: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(to bottom, #00000077, transparent);
  z-index: 2;
  padding: 10px 10%;
}

.header-portrait {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}

.header-intro-text {
  display: flex;
  flex-direction: column;
  color: white;
  font-size: 14px;
}

.more-case-studies {
  display: flex;
  flex-direction: column;
  flex-basis: 67%;
  gap: 10px;
}

.footer-intro-and-contact-button {
  display: flex;
  flex-direction: column;
  gap: 5px;
  justify-content: center;
  width: max-content;
}

.footer-contact-button-container {
  display: flex;
  justify-content: center;
}

.footer-contact-button {
  display: inline-block;
  background-color: #FFFFFF;
  padding: 10px 16px;
  font-weight: 600;
  font-size: 12px;
  color: #778390;
  border-radius: 18px;
  &:hover {
    text-decoration: none;
  }
}

.footer-about-me {
  display: flex;
  flex-direction: column;
  flex-basis: 33%;
  gap: 5px;
  justify-content: flex-start;
}

.case-study-outer-paragraphs {
  display: flex;
  gap: 2em;
}

.case-study-inner-paragraphs {
  display: flex;
  flex-basis: 50%;
  gap: 2em;
}

.case-study-individual-paragraph {
  flex-basis: 50%;
}

@media (max-width: 1024px) {
  .more-work {
    flex-direction: column;
  }
  .footer-about-me {
    flex-basis: 100%;
    justify-content: center;
  }
  .footer-intro-and-contact-button {
    width: 100%;
  }
  .more-case-studies {
    flex-basis: 100%;
  }
  .case-study-outer-paragraphs {
    flex-direction: column;
  }
  .case-study-inner-paragraphs {
    flex-basis: 100%;
  }
  .recent-case-study {
    flex: 3 0 calc(100% - 15px);
  }
}

@media (max-width: 768px) {
  .case-study-inner-paragraphs {
    flex-direction: column;
  }
  .case-study-individual-paragraph {
    flex-basis: 100%;
  }
  .recent-case-study {
    flex: 3 0 calc(100% - 15px);
  }
}
